
.skill {
  width: 300px;
  border: 1px solid;
  display: flex;
  flex-direction: column;
  align-items: center;

}
.icon {
  height: 60px;
  width: 60px;
  background: rgba(255, 255, 255, 0.1);
  transform: rotate(45deg);
  margin-bottom: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.icon img {
  transform: rotate(-45deg);
  width: 100%;

}

.description {
  display: flex;
  max-width: 180px;
  text-align: center;
}
.skill_title {
  font-family: 'Poppins', sans-serif;
  text-transform: uppercase;
  margin: 0 0 15px;
  padding: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.2;
}

